import { useEffect, useRef, useState } from 'react';
import Col from 'react-bootstrap/esm/Col';
import Form from 'react-bootstrap/esm/Form';
import Row from 'react-bootstrap/esm/Row';
import Spinner from 'react-bootstrap/esm/Spinner';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { useUseCaseCallback, useUseCaseMemo } from '../../core/usecase';
import { GenerateClassName } from '../../core/utils';
import Button from '../button/Button';
import { UploadFileUseCase } from '../files/usecases/uploadFileUseCase';
import { ProductWeightType } from './models/product';
import { CreateProductUseCase } from './usecases/createProductUseCase';
import { GetProductVitaminNamesUseCase } from './usecases/getProductVitaminNamesUseCase';

interface ProductNewPageProps {
    className?: string;
}

function ProductNewPageComp(props: ProductNewPageProps) {
    const [name, setName] = useState('');
    const [weightType, setWeightType] = useState<ProductWeightType>(
        ProductWeightType.GRAMS
    );
    const [calories, setCalories] = useState(0);
    const [proteins, setProteins] = useState(0);
    const [fats, setFats] = useState(0);
    const [carbohydrates, setCarbohydrates] = useState(0);
    const [vitamins, setVitamins] = useState<{ [key: string]: number }>();
    const [vitaminNames, setVitaminNames] =
        useState<{ [key: string]: string }>();
    const [isCreating, setCreating] = useState(false);

    const imgRef = useRef<HTMLInputElement>(null);

    const history = useHistory();

    const productVitaminNames = useUseCaseMemo(GetProductVitaminNamesUseCase);
    const createProductUseCase = useUseCaseCallback(CreateProductUseCase);
    const uploadFileUseCase = useUseCaseCallback(UploadFileUseCase);

    const gcm = GenerateClassName(props.className);

    useEffect(() => {
        productVitaminNames.then((productVitamins) => {
            const newNames: { [key: string]: string } = {};
            for (const productVitamin of productVitamins) {
                newNames[productVitamin.id] = productVitamin.name;
            }

            setVitaminNames((vitaminNames) => ({
                ...vitaminNames,
                ...newNames,
            }));
            setVitamins((vitamins) => {
                const result = { ...(vitamins ?? {}) };

                for (const productVitamin of productVitamins) {
                    if (!(productVitamin.id in result)) {
                        result[productVitamin.id] = 0;
                    }
                }

                return result;
            });
        });
    }, [productVitaminNames]);

    const onCreateProduct = async () => {
        setCreating(true);


        try {
            if (!imgRef.current?.files?.length) {
                throw new Error();
            }
            const file = await uploadFileUseCase({
                blob: imgRef.current.files[0],
            });
            const id = await createProductUseCase({
                name,
                img: file.url,
                weightType,
                calories,
                proteins,
                fats,
                carbohydrates,
                vitamins: Object.entries(vitamins ?? {}).map(
                    ([vitamin, count]) => ({
                        id: parseInt(vitamin),
                        count,
                    })
                ),
            });

            history.push(`/product/${id}`);
        } catch (e) {}
        setCreating(false);
    };

    return (
        <div className={props.className}>
            <Form.Group className="mb-3" controlId="formName">
                <Form.Label>Название</Form.Label>
                <Form.Control
                    readOnly={isCreating}
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    size="sm"
                    type="text"
                    placeholder="Введите название"
                />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formImg">
                <Form.Label>Картинка</Form.Label>
                <Form.Control
                    readOnly={isCreating}
                    ref={imgRef}
                    size="sm"
                    type="file"
                    placeholder="Выберите картинку"
                />
            </Form.Group>
            <Form.Group className="mb-3">
                <Form.Label>Измеряется в</Form.Label>
                <div className={gcm('&weight-type_container')}>
                    <Form.Check
                        readOnly={isCreating}
                        value={ProductWeightType.GRAMS}
                        checked={weightType === ProductWeightType.GRAMS}
                        onChange={(e) => {
                            if (e.target.checked) {
                                setWeightType(ProductWeightType.GRAMS);
                            }
                        }}
                        type="radio"
                        name="weighttype"
                        label="Граммы"
                        id={`weightType ${ProductWeightType.GRAMS}`}
                    />
                    <Form.Check
                        readOnly={isCreating}
                        value={ProductWeightType.MILLILITERS}
                        checked={weightType === ProductWeightType.MILLILITERS}
                        onChange={(e) => {
                            if (e.target.checked) {
                                setWeightType(ProductWeightType.MILLILITERS);
                            }
                        }}
                        type="radio"
                        name="weighttype"
                        label="Миллилитры"
                        id={`weightType ${ProductWeightType.MILLILITERS}`}
                    />
                </div>
            </Form.Group>
            <div className="mb-3">
                <div>
                    КБЖУ на 100
                    {weightType === ProductWeightType.GRAMS ? 'гр' : 'мл'}
                </div>
                <Row>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Каллории</Form.Label>
                            <Form.Control
                                readOnly={isCreating}
                                value={calories}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    setCalories(Number.isNaN(num) ? 0 : num);
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Белки</Form.Label>
                            <Form.Control
                                readOnly={isCreating}
                                value={proteins}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    setProteins(Number.isNaN(num) ? 0 : num);
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Жиры</Form.Label>
                            <Form.Control
                                readOnly={isCreating}
                                value={fats}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    setFats(Number.isNaN(num) ? 0 : num);
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Углеводы</Form.Label>
                            <Form.Control
                                readOnly={isCreating}
                                value={carbohydrates}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    setCarbohydrates(
                                        Number.isNaN(num) ? 0 : num
                                    );
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                </Row>
            </div>
            <div className="mb-3">
                <div>Витамины</div>
                {vitamins == null && (
                    <Spinner animation="grow" variant="info" />
                )}
                {vitamins == null ||
                    Object.keys(vitamins).map((vitaminId) => (
                        <div key={vitaminId}>
                            {vitaminNames![vitaminId]}
                            <input
                                disabled={isCreating}
                                type="number"
                                value={vitamins[vitaminId]}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    setVitamins({
                                        ...vitamins,
                                        [vitaminId]: Number.isNaN(num)
                                            ? 0
                                            : num,
                                    });
                                }}
                            />
                        </div>
                    ))}
            </div>
            <div>
                <Button disabled={isCreating} onClick={onCreateProduct}>
                    Создать продукт{' '}
                    {isCreating && (
                        <Spinner animation="grow" variant="info" size="sm" />
                    )}
                </Button>
            </div>
        </div>
    );
}

const ProductNewPage = styled(ProductNewPageComp)`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    &__weight-type_container {
        display: flex;

        & > div {
            margin: 0 8px 0 8px;
        }
    }

    &__cpfc {
    }
`;

export default ProductNewPage;
