import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { colorOrange } from '../../core/colors';
import { sleep } from '../../core/utils';
import { UserContext } from '../user/userContext';

interface MenuDesktopProps {
    className?: string;
    fixed: boolean;
}

function MenuDesktopComp(props: MenuDesktopProps) {
    const userContext = useContext(UserContext);
    const [height, setHeight] = useState(userContext?.user == null ? 0 : 59);
    const [hidden, setHidden] = useState(!userContext?.user);

    useEffect(() => {
        if (!hidden) {
            setHeight(59);
        }
    }, [hidden]);

    useEffect(() => {
        if (userContext?.user == null) {
            setHeight(0);
            (async () => {
                await sleep(350);
                setHidden(true);
            })();
        } else {
            setHidden(false);
        }
    }, [userContext?.user]);

    if (hidden) {
        return null;
    }

    return (
        <div className={props.className} style={{ height }}>
            <div className="mx-2">
                <Link to="/menu">Меню</Link>
            </div>
            <div className="mx-2">
                <Link to="/cart">Корзина</Link>
            </div>
            {userContext?.user?.roles?.some(role => role === 'master') && (
                <React.Fragment>
                    {' '}
                    <div className="mx-2">
                        <Link to="/products">Продукты</Link>
                    </div>
                    <div className="mx-2">
                        <Link to="/recipes">Рецепты</Link>
                    </div>
                    <div className="mx-2">
                        <Link to="/clients">Клиенты</Link>
                    </div>
                </React.Fragment>
            )}
        </div>
    );
}

const MenuDesktop = styled(MenuDesktopComp)`
    background-color: white;
    overflow: hidden;
    transition: height 350ms;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    border-bottom: 1px solid #e8e8e8;

    & a {
        font-size: 16px;
        color: black;
        text-decoration: none;
        font-weight: bold;
        text-transform: uppercase;
        transition: text-shadow 350ms;

        &:hover {
            color: black;
            text-shadow: 0 0 1px ${colorOrange};
        }
    }
`;

export default MenuDesktop;
