import React, { Suspense, useContext } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import CartPage from './features/cart/pages/CartPage';
import ClientListPage from './features/client/pages/ClientListPage';
import LoadingPage from './features/loading/LoadingPage';
import Page from './features/page/Page';
import ProductEditPage from './features/product/ProductEditPage';
import ProductNewPage from './features/product/ProductNewPage';
import ProductPage from './features/product/ProductPage';
import RecipeNewPage from './features/recipe/pages/RecipeNewPage';
import RecipePage from './features/recipe/pages/RecipePage';
import RecipesListPage from './features/recipe/pages/RecipesListPage';
import { UserContext } from './features/user/userContext';

const MainPage = React.lazy(() => import('./features/main/MainPage'));
const AuthPage = React.lazy(() => import('./features/auth/AuthPage'));
const MenuPage = React.lazy(() => import('./features/menu/MenuPage'));
const SignOutPage = React.lazy(() => import('./features/signout/SignOutPage'));
const ProductsPage = React.lazy(
    () => import('./features/product/ProductsPage')
);

export function Router() {
    const userContext = useContext(UserContext);
    const canRender = !!userContext;

    return (
        <BrowserRouter>
            <Page>
                {canRender || <LoadingPage />}
                {canRender && (
                    <Suspense fallback={<LoadingPage />}>
                        <Switch>
                            {userContext?.user?.roles.some(
                                (role) => role === 'master'
                            ) && masterRoutes}
                            {userContext?.user?.roles.some(
                                (role) => role === 'user'
                            ) && userRoutes}
                            <Route path="/auth" exact>
                                <AuthPage />
                            </Route>
                            <Route path="/signout" exact>
                                <SignOutPage />
                            </Route>
                            <Route path="/" exact>
                                <MainPage />
                            </Route>
                            <Route path="*">
                                <h1>Not found</h1>
                            </Route>
                        </Switch>
                    </Suspense>
                )}
            </Page>
        </BrowserRouter>
    );
}

const userRoutes: JSX.Element[] = [
    <Route
        key="menu"
        path="/menu"
        render={(props) => {
            const queryParams = new URLSearchParams(props.location.search);
            const type = queryParams.get('type');

            return <MenuPage type={type} />;
        }}
        exact
    />,
    <Route key="cart" path="/cart" exact>
        <CartPage />
    </Route>,
];
const masterRoutes: JSX.Element[] = [
    <Route key="products" path="/products" exact>
        <ProductsPage />
    </Route>,
    <Route key="product/new" path="/product/new" exact>
        <ProductNewPage />
    </Route>,
    <Route
        key="product"
        path="/product/:id"
        render={(props) => (
            <ProductPage productId={parseInt(props.match.params.id)} />
        )}
        exact
    />,
    <Route
        key="product"
        path="/product/edit/:id"
        render={(props) => (
            <ProductEditPage productId={parseInt(props.match.params.id)} />
        )}
        exact
    />,
    <Route key="recipes" path="/recipes" exact>
        <RecipesListPage />
    </Route>,
    <Route key="recipe-new" path="/recipe/new" exact>
        <RecipeNewPage />
    </Route>,
    <Route
        key="recipe-edit"
        path="/recipe/edit/:id"
        render={(props) => <RecipeNewPage />}
        exact
    />,
    <Route
        key="recipe"
        path="/recipe/:id"
        render={(props) => (
            <RecipePage recipeId={parseInt(props.match.params.id)} />
        )}
        exact
    />,
    <Route key="clients" path="/clients" exact>
        <ClientListPage />
    </Route>,
];
