import { Spinner } from 'react-bootstrap';
import Col from 'react-bootstrap/esm/Col';
import Row from 'react-bootstrap/esm/Row';
import { FaEdit, FaTrashAlt } from 'react-icons/fa';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import {
    UseCasePromisedStatus,
    useUseCaseCallback,
    useUseCasePromisedMemo,
} from '../../core/usecase';
import { GenerateClassName } from '../../core/utils';
import Button from '../button/Button';
import Link from '../link/Link';
import { ProductWeightType } from './models/product';
import { DeleteProductUseCase } from './usecases/deleteProductUseCase';
import { GetProductByIdUseCase } from './usecases/getProductByIdUseCase';

export interface ProductPageProps {
    className?: string;
    productId: number;
}

export function ProductPageComp(props: ProductPageProps) {
    const gcm = GenerateClassName(props.className);
    const history = useHistory();

    const product = useUseCasePromisedMemo(
        GetProductByIdUseCase,
        props.productId
    );

    const deleteProduct = useUseCaseCallback(DeleteProductUseCase);

    if (product.status === UseCasePromisedStatus.PENDING) {
        return (
            <div className="d-flex justify-content-center w-100 p-3">
                <Spinner animation="grow" variant="info" />
            </div>
        );
    }

    if (product.status === UseCasePromisedStatus.ERROR) {
        return (
            <div className="d-flex justify-content-center w-100 p-3">
                <span>Error</span>
            </div>
        );
    }

    const onDelete = async () => {
        await deleteProduct(product.result.id);
        history.push('/products');
    };

    return (
        <div className={props.className}>
            <div className="d-flex">
                <Link
                    href={{
                        pathname: `/product/edit/${product.result.id}`,
                        search: new URLSearchParams({
                            from: `/product/${product.result.id}`,
                        }).toString(),
                    }}
                    className="d-flex align-items-center"
                >
                    <FaEdit size={17} />
                    &nbsp;Изменить
                </Link>
                <Button
                    onClick={onDelete}
                    className="d-flex align-items-center"
                >
                    <FaTrashAlt size={17} />
                    &nbsp;Удалить
                </Button>
            </div>
            <div>{product.result.name}</div>
            <div>
                <img
                    className={gcm('&img')}
                    src={product.result.img}
                    alt={product.result.name}
                />
            </div>
            <div>
                На 100
                {product.result.weightType === ProductWeightType.GRAMS
                    ? 'гр'
                    : 'мл'}
                :
            </div>
            <div>
                <Row>
                    <Col xs={6}>
                        <div>Калории: {product.result.calories}</div>
                    </Col>
                    <Col xs={6}>
                        <div>Белки: {product.result.proteins}</div>
                    </Col>
                    <Col xs={6}>
                        <div>Жиры: {product.result.fats}</div>
                    </Col>
                    <Col xs={6}>
                        <div>Углеводы: {product.result.carbohydrates}</div>
                    </Col>
                </Row>
            </div>
            <Row>
                {product.result.vitamins.map((vitamin) => (
                    <Col xs={3} key={vitamin.vitamin.id}>
                        {vitamin.vitamin.name}:&nbsp;{vitamin.count}
                    </Col>
                ))}
            </Row>
        </div>
    );
}

const ProductPage = styled(ProductPageComp)`
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1.5rem;
    align-items: center;

    &__img {
        width: 100%;
        max-width: 400px;
        object-fit: cover;
    }
`;

export default ProductPage;
