import { useState } from 'react';
import Badge from 'react-bootstrap/esm/Badge';
import Col from 'react-bootstrap/esm/Col';
import Row from 'react-bootstrap/esm/Row';
import Spinner from 'react-bootstrap/esm/Spinner';
import { FaEdit, FaTrashAlt } from 'react-icons/fa';
import styled from 'styled-components';
import { useUseCaseCallback } from '../../core/usecase';
import Button from '../button/Button';
import Link from '../link/Link';
import { Product as ProductType } from './models/product';
import { DeleteProductUseCase } from './usecases/deleteProductUseCase';

interface ProductProps {
    className?: string;
    onDelete?: () => void;
    product: ProductType;
}

function ProductComp(props: ProductProps) {
    const [isDeleting, setDeleting] = useState(false);

    const deleteProduct = useUseCaseCallback(DeleteProductUseCase);

    const onDelete = async () => {
        setDeleting(true);
        try {
            await deleteProduct(props.product.id);
            props.onDelete ? props.onDelete() : void 0;
        } catch (e) {}
        setDeleting(false);
    };

    return (
        <div className={props.className}>
            <div
                className="position-relative flex-shrink-0 me-3"
                style={{ width: 100 }}
            >
                <img
                    className="position-absolute"
                    style={{
                        top: 0,
                        left: 0,
                        width: '100%',
                        height: '100%',
                    }}
                    src={props.product.img}
                    alt=""
                />
            </div>
            <div className="flex-grow-1 me-3 overflow-hidden">
                <div className="d-flex justify-content-between align-items-center mb-3">
                    <Link
                        href={`/product/${props.product.id}`}
                        className="fw-bold me-3"
                        style={{ overflow: 'hidden', textOverflow: 'ellipsis' }}
                    >
                        {props.product.name}
                    </Link>
                    <div className="flex-shrink-0">
                        <span className="">
                            <Link href={`/product/edit/${props.product.id}`}>
                                <FaEdit />
                                &nbsp;Изменить
                            </Link>
                        </span>
                        <span className="ms-2">
                            <Button onClick={onDelete}>
                                <FaTrashAlt />
                                &nbsp;Удалить
                            </Button>
                        </span>
                    </div>
                </div>
                <div className="d-flex justify-content-between">
                    <Row>
                        {props.product.vitamins.map((vitamin) => (
                            <Col
                                key={vitamin.vitamin.id}
                                xs="4"
                                sm="3"
                                md="2"
                                lg="4"
                                xl="3"
                                xxl="2"
                            >
                                <div>
                                    <Badge>
                                        {vitamin.vitamin.name}:&nbsp;
                                        {vitamin.count}
                                    </Badge>
                                </div>
                            </Col>
                        ))}
                    </Row>
                    <div className="ms-3">
                        <div
                            className="ps-1 pt-1 pb-1"
                            style={{ borderLeft: '1px solid gray' }}
                        >
                            <div className="">
                                Каллории:&nbsp;{props.product.calories}
                            </div>
                            <div className="">
                                Белки:&nbsp;{props.product.proteins}
                            </div>
                            <div className="">
                                Жиры:&nbsp;{props.product.fats}
                            </div>
                            <div className="mb-3">
                                Углеводы:&nbsp;
                                {props.product.carbohydrates}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {isDeleting && (
                <div className={`${props.className}__over`}>
                    <Spinner animation="grow" variant="info" />
                </div>
            )}
        </div>
    );
}

const Product = styled(ProductComp)`
    display: flex;
    overflow: hidden;
    min-height: 100px;
    background-color: #cdcdcd30;
    padding: 0.5rem;
    border-radius: 8px;
    position: relative;

    & img {
        border-radius: 8px;
        object-fit: cover;
    }

    &__over {
        position: absolute;
        inset: 0;
        background-color: #e0e0e0aa;
        display: flex;
        justify-content: center;
        align-items: center;
    }
`;

export default Product;
