import React, { useEffect, useState } from 'react';
import { useUseCaseMemo } from '../../core/usecase';
import { User } from './models/user';
import { GetCurrentUserUseCase } from './usecases/getCurrentUserUseCase';
import { UserContext, UserContextValue } from './userContext';

export function UserProvider(props: { children?: React.ReactChild }) {
    const [userContext, setUserContext] = useState<UserContextValue | null>(
        null
    );

    const user = useUseCaseMemo(GetCurrentUserUseCase);

    useEffect(() => {
        function setUser(user: User | null) {
            setUserContext({ user, setUser });
        }

        user.then((value) => {
            setUser(value);
        });
    }, [user]);

    return (
        <UserContext.Provider value={userContext}>
            {props.children}
        </UserContext.Provider>
    );
}
