import React from 'react';
import Container from 'react-bootstrap/esm/Container';
import styled from 'styled-components';
import Content from './Content';
import Footer from './Footer';
import Header from './Header';
import MenuDesktop from './MenuDesktop';

interface PageProps {
    className?: string;
    children?:
        | React.ReactChild
        | React.ReactChild[]
        | React.ReactElement
        | React.ReactElement[]
        | React.ReactNode
        | React.ReactNode[];
}

function PageComp(props: PageProps) {
    return (
        <Container
            style={{
                backgroundColor: '#FFFFFFEA',
                boxShadow: '0px 0px 5px #E4E4E4',
            }}
            className="p-0"
        >
            <div className={props.className}>
                <div style={{ opacity: 0 }}>
                    <Header fixed={false} />
                    <MenuDesktop fixed={false} />
                </div>
                <div className={props.className + '__fixed'} style={{}}>
                    <Header fixed />
                    <MenuDesktop fixed />
                </div>
                <Content>{props.children}</Content>
                <Footer />
            </div>
        </Container>
    );
}

const Page = styled(PageComp)`
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    &__fixed {
        @media (min-width: 576px) {
            max-width: 540px;
        }
        @media (min-width: 768px) {
            max-width: 720px;
        }
        @media (min-width: 992px) {
            max-width: 960px;
        }
        @media (min-width: 1200px) {
            max-width: 1140px;
        }
        @media (min-width: 1400px) {
            max-width: 1320px;
        }
        width: 100%;
        position: fixed;
        z-index: 50;
        box-shadow: rgb(228 228 228) 0px 5px 5px;
        top: 0;
    }
`;

export default Page;
