import { useRef, useState } from 'react';
import Col from 'react-bootstrap/esm/Col';
import Form from 'react-bootstrap/esm/Form';
import Row from 'react-bootstrap/esm/Row';
import Spinner from 'react-bootstrap/esm/Spinner';
import { FaArrowCircleLeft } from 'react-icons/fa';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import {
    UseCasePromisedStatus,
    useUseCaseCallback,
    useUseCasePromisedMemo
} from '../../core/usecase';
import { GenerateClassName } from '../../core/utils';
import Button from '../button/Button';
import { ProductWeightType } from './models/product';
import {
    EditProductUseCase,
    ProductEddited
} from './usecases/editProductUseCase';
import { GetProductByIdUseCase } from './usecases/getProductByIdUseCase';
import { GetProductVitaminNamesUseCase } from './usecases/getProductVitaminNamesUseCase';

interface ProductEditPageProps {
    productId: number;
    className?: string;
}

function ProductEditPageComp(props: ProductEditPageProps) {
    // Utils and calculated
    const history = useHistory();
    const gcm = GenerateClassName(props.className);

    // Pending vars
    const product = useUseCasePromisedMemo(
        GetProductByIdUseCase,
        props.productId
    );
    const vitamins = useUseCasePromisedMemo(GetProductVitaminNamesUseCase);

    // UseState
    const [isSaving, setSaving] = useState(false);
    const [editedProduct, setEditedProduct] = useState<ProductEddited>({});
    const imgRef = useRef<HTMLInputElement>(null);

    // UseCases
    const editProductUseCase = useUseCaseCallback(EditProductUseCase);

    // UseEffect

    if (product.status === UseCasePromisedStatus.PENDING) {
        return (
            <div className="d-flex justify-content-center w-100 p-3">
                <Spinner animation="grow" variant="info" />
            </div>
        );
    }

    if (product.status === UseCasePromisedStatus.ERROR) {
        return (
            <div className="d-flex justify-content-center w-100 p-3">
                <span>Error</span>
            </div>
        );
    }

    const changeEditedProduct = (props: ProductEddited) => {
        setEditedProduct({ ...editedProduct, ...props });
    };

    const productUnion = {
        ...product.result,
        ...editedProduct,
        vitamins:
            editedProduct.vitamins ??
            product.result.vitamins.reduce<
                Exclude<ProductEddited['vitamins'], undefined>
            >((acc, val) => {
                acc.push({ id: val.vitamin.id, count: val.count });
                return acc;
            }, []),
    };

    const onSave = async () => {
        setSaving(true);

        try {
            await editProductUseCase(product.result.id, editedProduct);
        } catch (e) {}
        setSaving(false);
    };

    return (
        <div className={props.className}>
            <div className="mb-3">
                <Button onClick={() => history.goBack()}>
                    <FaArrowCircleLeft size={17} />
                    &nbsp;Назад
                </Button>
            </div>
            <Form.Group className="mb-3" controlId="formName">
                <Form.Label>Название</Form.Label>
                <Form.Control
                    readOnly={isSaving}
                    value={productUnion.name}
                    onChange={(e) =>
                        changeEditedProduct({ name: e.target.value })
                    }
                    size="sm"
                    type="text"
                    placeholder="Введите название"
                />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formImg">
                <Form.Label>Картинка</Form.Label>
                <img
                    alt={'Картинка ' + productUnion.name}
                    src={productUnion.img}
                    style={{
                        display: 'block',
                        width: '100%',
                        maxWidth: 400,
                        maxHeight: 400,
                        objectFit: 'cover',
                    }}
                />
                <Form.Control
                    readOnly={isSaving}
                    ref={imgRef}
                    size="sm"
                    type="file"
                    placeholder="Выберите картинку"
                />
            </Form.Group>
            <Form.Group className="mb-3">
                <Form.Label>Измеряется в</Form.Label>
                <div className={gcm('&weight-type_container')}>
                    <Form.Check
                        disabled={isSaving}
                        value={ProductWeightType.GRAMS}
                        checked={
                            productUnion.weightType === ProductWeightType.GRAMS
                        }
                        onChange={(e) => {
                            if (e.target.checked) {
                                changeEditedProduct({
                                    weightType: ProductWeightType.GRAMS,
                                });
                            }
                        }}
                        type="radio"
                        name="weighttype"
                        label="Граммы"
                        id={`weightType ${ProductWeightType.GRAMS}`}
                    />
                    <Form.Check
                        disabled={isSaving}
                        value={ProductWeightType.MILLILITERS}
                        checked={
                            productUnion.weightType ===
                            ProductWeightType.MILLILITERS
                        }
                        onChange={(e) => {
                            if (e.target.checked) {
                                changeEditedProduct({
                                    weightType: ProductWeightType.MILLILITERS,
                                });
                            }
                        }}
                        type="radio"
                        name="weighttype"
                        label="Миллилитры"
                        id={`weightType ${ProductWeightType.MILLILITERS}`}
                    />
                </div>
            </Form.Group>
            <div className="mb-3">
                <div>
                    КБЖУ на 100
                    {productUnion.weightType === ProductWeightType.GRAMS
                        ? 'гр'
                        : 'мл'}
                </div>
                <Row>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Каллории</Form.Label>
                            <Form.Control
                                readOnly={isSaving}
                                value={productUnion.calories}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    changeEditedProduct({
                                        calories: Number.isNaN(num) ? 0 : num,
                                    });
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Белки</Form.Label>
                            <Form.Control
                                readOnly={isSaving}
                                value={productUnion.proteins}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    changeEditedProduct({
                                        proteins: Number.isNaN(num) ? 0 : num,
                                    });
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Жиры</Form.Label>
                            <Form.Control
                                readOnly={isSaving}
                                value={productUnion.fats}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    changeEditedProduct({
                                        fats: Number.isNaN(num) ? 0 : num,
                                    });
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="formName">
                            <Form.Label>Углеводы</Form.Label>
                            <Form.Control
                                readOnly={isSaving}
                                value={productUnion.carbohydrates}
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    changeEditedProduct({
                                        carbohydrates: Number.isNaN(num)
                                            ? 0
                                            : num,
                                    });
                                }}
                                size="sm"
                                type="number"
                            />
                        </Form.Group>
                    </Col>
                </Row>
            </div>
            <div className="mb-3">
                <div>Витамины</div>
                {vitamins.status === UseCasePromisedStatus.PENDING && (
                    <Spinner animation="grow" variant="info" />
                )}
                {vitamins.status === UseCasePromisedStatus.ERROR && (
                    <div>ERROR</div>
                )}
                {vitamins.status === UseCasePromisedStatus.SUCCESS &&
                    vitamins.result.map((vitamin) => (
                        <div key={vitamin.id}>
                            {vitamin.name}
                            <input
                                disabled={isSaving}
                                type="number"
                                value={
                                    productUnion.vitamins[vitamin.id]?.count ??
                                    0
                                }
                                onChange={(e) => {
                                    const num = parseInt(e.target.value);
                                    let wasChanged = false;
                                    changeEditedProduct({
                                        vitamins: productUnion.vitamins.map(
                                            (vitaminLocal) => {
                                                if (
                                                    vitaminLocal.id ===
                                                    vitamin.id
                                                ) {
                                                    wasChanged = true;
                                                    return {
                                                        id: vitamin.id,
                                                        count: Number.isNaN(num)
                                                            ? 0
                                                            : num,
                                                    };
                                                }

                                                return vitaminLocal;
                                            }
                                        ),
                                    });
                                    if (!wasChanged) {
                                        changeEditedProduct({
                                            vitamins: [
                                                ...productUnion.vitamins,
                                                {
                                                    id: vitamin.id,
                                                    count: Number.isNaN(num)
                                                        ? 0
                                                        : num,
                                                },
                                            ],
                                        });
                                    }
                                }}
                            />
                        </div>
                    ))}
            </div>
            <div>
                <Button disabled={isSaving} onClick={onSave}>
                    Сохранить изменения&nbsp;
                    {isSaving && (
                        <Spinner animation="grow" variant="info" size="sm" />
                    )}
                </Button>
            </div>
        </div>
    );
}

const ProductEditPage = styled(ProductEditPageComp)`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 1.5rem;
`;

export default ProductEditPage;
