import styled from 'styled-components';

interface SocialProps {
    className?: string;
    href: string;
    color: string;
    children: React.ReactNode;
}

function SocialComp(props: SocialProps) {
    return (
        <a href={props.href} target="_blank" rel="noreferrer" className={props.className + ' me-1'}>
            {props.children}
        </a>
    );
}

const Social = styled(SocialComp)`
    box-sizing: border-box;
    border-radius: 100%;
    overflow: hidden;
    background-color: ${(props) => props.color};
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
`;

export default Social;
