import React from 'react';
import { Link as LinkRouter } from 'react-router-dom';
import styled from 'styled-components';
import { colorOrange } from '../../core/colors';

interface LinkProps {
    className?: string;
    href: React.ComponentProps<LinkRouter>['to'];
    children?: React.ReactChild | React.ReactChild[];
    style?: React.CSSProperties;
}

function LinkComp(props: LinkProps) {
    return (
        <LinkRouter to={props.href} className={props.className} style={props.style}>
            {props.children}
        </LinkRouter>
    );
}

const Link = styled(LinkComp)`
    color: ${colorOrange};
    text-decoration: none;
    transition: color 350ms;

    &:hover {
        color: black;
    }
`;

export default Link;
