import React, { useContext, useEffect, useState } from 'react';
import Col from 'react-bootstrap/esm/Col';
import Row from 'react-bootstrap/esm/Row';
import Spinner from 'react-bootstrap/esm/Spinner';
import {
    FaFacebookF,
    FaInstagram,
    FaSignInAlt,
    FaSignOutAlt,
    FaTelegramPlane,
    FaTwitter,
    FaUserCircle,
    FaVk,
    FaYoutube
} from 'react-icons/fa';
import styled from 'styled-components';
import logo from '../../static/logo.png';
import Button from '../button/Button';
import Link from '../link/Link';
import { UserContext } from '../user/userContext';
import Social from './Social';

interface HeaderProps {
    className?: string;
    fixed: boolean;
}

const MaxPadding = 16;

function HeaderComp(props: HeaderProps) {
    const getPaddingFromScroll = () => {
        return Math.max(4, MaxPadding - window.scrollY / 2);
    };
    const getLogoHeightScroll = () => {
        return Math.max(40, 59 - window.scrollY / 2);
    };

    const userContext = useContext(UserContext);
    const [padding, setPadding] = useState(() => getPaddingFromScroll());
    const [logoHeight, setLogoHeight] = useState(getLogoHeightScroll());

    useEffect(() => {
        if (props.fixed) {
            const callback = (e: Event) => {
                setPadding(getPaddingFromScroll());
                setLogoHeight(getLogoHeightScroll());
            };

            window.addEventListener('scroll', callback);

            return () => {
                window.removeEventListener('scroll', callback);
            };
        }
    }, []);

    const enterOrPrivateLink =
        userContext == null ? (
            <Spinner animation="grow" size="sm" variant="info" />
        ) : userContext.user ? (
            <Link
                href="/profile"
                style={{ display: 'flex', alignItems: 'center' }}
            >
                <FaUserCircle />
                &nbsp;Личный кабинет
            </Link>
        ) : (
            <Link
                href="/auth"
                style={{ display: 'flex', alignItems: 'center' }}
            >
                <FaSignInAlt />
                &nbsp;Войти
            </Link>
        );

    return (
        <div
            className={props.className + ' px-3'}
            style={{
                paddingTop: props.fixed ? padding : MaxPadding,
                paddingBottom: props.fixed ? padding : MaxPadding,
            }}
        >
            <Row>
                <Col className="d-flex justify-content-left align-items-center">
                    <div>
                        <Link href="/">
                            <img
                                src={logo}
                                alt=""
                                style={{ height: logoHeight }}
                            />
                        </Link>
                    </div>
                </Col>
                <Col className="d-flex justify-content-center align-items-center ps-3">
                    <Social color="#4267B2" href="/">
                        <FaFacebookF />
                    </Social>
                    <Social color="#2787F5" href="/">
                        <FaVk />
                    </Social>
                    <Social color="#00ACEE" href="/">
                        <FaTwitter />
                    </Social>
                    <Social color="#0088CC" href="/">
                        <FaTelegramPlane />
                    </Social>
                    <Social color="#C4302B" href="/">
                        <FaYoutube />
                    </Social>
                    <Social color="#D8478F" href="/">
                        <FaInstagram />
                    </Social>
                </Col>
                <Col className="d-flex justify-content-end align-items-center">
                    {userContext?.user == null || (
                        <div className="mx-2">
                            <Button
                            >
                                <FaUserCircle/>
                                &nbsp;Андрей Л.
                            </Button>
                        </div>
                    )}
                    <div className="mx-2">{enterOrPrivateLink}</div>
                    {userContext?.user == null || (
                        <div className="mx-2">
                            <Link
                                href="/signout"
                                style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                }}
                            >
                                <FaSignOutAlt />
                                &nbsp;Выйти
                            </Link>
                        </div>
                    )}
                </Col>
            </Row>
        </div>
    );
}

const Header = styled(HeaderComp)`
    width: 100%;
    border-bottom: 1px solid #e8e8e8;
    background-color: white;
    z-index: 50;
`;

export default Header;
