export function sleep(duration: number) {
    return new Promise<void>((res) => {
        setTimeout(() => {
            res();
        }, duration);
    });
}

export function GenerateClassName(
    className?: string,
    separator: string = '__'
) {
    const prefix = className == null ? '' : className + separator;

    const makeClass = (className: string) => {
        if (className[0] === '&') {
            return prefix + className.slice(1);
        }
        return className;
    };

    return (
        ...classNames: (
            | void
            | null
            | string
            | string[]
            | { [key: string]: boolean }
        )[]
    ) => {
        const result: string[] = [];
        for (const className of classNames) {
            if (className == null) {
            } else if (typeof className === 'string') {
                result.push(makeClass(className));
            } else if (Array.isArray(className)) {
                result.push(
                    ...className.map((className) => makeClass(className))
                );
            } else {
                for (const classNameLocal in className) {
                    if (className[classNameLocal]) {
                        result.push(classNameLocal);
                    }
                }
            }
        }
        return result.join(' ');
    };
}
