import React from 'react';
import styled from 'styled-components';
import { colorOrange } from '../../core/colors';

interface ButtonProps {
    className?: string;
    children?:
        | React.ReactChild
        | React.ReactChild[]
        | JSX.Element
        | JSX.Element[]
        | React.ReactNode
        | React.ReactNode[];
    onClick?: () => void;
    disabled?: boolean;
}

function ButtonComp(props: ButtonProps) {
    return (
        <button
            disabled={props.disabled}
            onClick={props.onClick}
            className={props.className}
        >
            {props.children}
        </button>
    );
}

const Button = styled(ButtonComp)`
    border: none;
    background: none;
    color: ${colorOrange};

    &:active {
        color: black;
    }

    &:disabled {
        color: gray;
    }
`;

export default Button;
