import { useEffect, useMemo, useRef, useState } from 'react';

export type UseCaseSettings = {} | null;

export class UseCaseEvents {
    private static _settings: UseCaseSettings = null;
    private static _listeners: ((value: UseCaseSettings) => void)[] = [];

    static get settings() {
        return this._settings;
    }

    static setSettings(settings: UseCaseSettings) {
        this._settings = Object.freeze(settings);

        for (let listener of this._listeners) {
            listener(this._settings);
        }
    }

    static addListener(callback: (value: UseCaseSettings) => void) {
        this._listeners.push(callback);
    }

    static removeListener(callback: (value: UseCaseSettings) => void) {
        this._listeners = this._listeners.filter(
            (listener) => listener !== callback
        );
    }
}

export type UseCase<T, A extends any[]> = (
    settings: UseCaseSettings,
    ...args: A
) => T;

export function useUseCaseSettings() {
    const [settings, setSettings] = useState(UseCaseEvents.settings);

    useEffect(() => {
        const callback = (settings: UseCaseSettings) => {
            setSettings(settings);
        };

        UseCaseEvents.addListener(callback);

        return () => {
            UseCaseEvents.removeListener(callback);
        };
    }, []);

    return settings;
}

export enum UseCasePromisedStatus {
    PENDING,
    SUCCESS,
    ERROR,
}

export type UseCasePromised<T> =
    | { status: UseCasePromisedStatus.PENDING }
    | { status: UseCasePromisedStatus.SUCCESS; result: T }
    | { status: UseCasePromisedStatus.ERROR; error: Error };

export function useUseCasePromisedMemo<T extends Promise<any>, A extends any[]>(
    useCase: UseCase<T, A>,
    ...args: A
): UseCasePromised<Awaited<T>> {
    const settings = useUseCaseSettings();
    const [result, setResult] = useState<UseCasePromised<Awaited<T>>>({
        status: UseCasePromisedStatus.PENDING,
    });
    const update = useRef(0);
    const prevArgs = useRef<A>(args);

    useEffect(() => {
        if (
            result.status === UseCasePromisedStatus.SUCCESS &&
            prevArgs.current === args
        ) {
            update.current--;
        } else {
            prevArgs.current = args
            const updateLocal = update;

            setResult({ status: UseCasePromisedStatus.PENDING });
            const updateId = ++updateLocal.current;
            // eslint-disable-next-line react-hooks/rules-of-hooks
            useCase(settings, ...args)
                .then((result) => {
                    if (updateId === update.current) {
                        setResult({
                            status: UseCasePromisedStatus.SUCCESS,
                            result,
                        });
                    }
                })
                .catch((error) => {
                    if (updateId === update.current) {
                        setResult({
                            status: UseCasePromisedStatus.ERROR,
                            error,
                        });
                    }
                });

            return () => {
                updateLocal.current++;
            };
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [settings, ...args]);

    return result;
}

export function useUseCaseMemo<T, A extends any[]>(
    useCase: UseCase<T, A>,
    ...args: A
): T {
    const settings = useUseCaseSettings();

    return useMemo(() => {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        return useCase(settings, ...args) as T;
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [settings, ...args]);
}

export function useUseCaseCallback<T, A extends any[]>(
    useCase: UseCase<T, A>
): (...args: A) => T {
    const settings = useUseCaseSettings();

    return useMemo(() => {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        return (...args: A) => useCase(settings, ...args) as T;
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [settings]);
}
